.calendar {
  background-color: #0d2e620d;
  &__title {
    border-left: none;
    padding-left: 0;
    text-align: center;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: var(--mb-2);
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    border-radius: var(--border-radius);
    height: 100%;
    background-color: var(--light-color);

    &-top {
      border-top-left-radius: var(--border-radius);
      border-top-right-radius: var(--border-radius);
      padding: var(--mb-1);
      color: var(--orange-color);
      background-color: var(--blue-color);
    }

    &-title {
      margin-bottom: var(--mb-1);
      font-size: clamp(1.25rem, 1.143rem + 0.54vw, 1.625rem);
      color: var(--orange-color);
      text-align: center;
      line-height: var(--line-height);
    }


    &-ads {
      font-size: 12px;
      text-align: center;
      color: var(--white-color);

    }

    &-body {
      flex-grow: 1;
      padding: var(--mb-1);
    }

    &-subtitle {
      margin-bottom: var(--mb-1);
      font-size: clamp(1.125rem, 1.054rem + 0.36vw, 1.375rem);
      color: var(--dark-blue-color);
      line-height: var(--line-height);
      text-transform: uppercase;
    }

    &-list {
      margin-bottom: var(--mb-1);
      padding-left: var(--mb-2);
      list-style: square;
    }

    &-emag {
      margin-bottom: var(--mb-1);
      font-size: 14px;
      font-weight: var(--semibold-font);
      color: var(--dark-color);
      line-height: var(--line-height);
    }

    &-text {
      line-height: var(--line-height);
    }
  }

  &__link {
    position: relative;
    display: block;
    max-width: 280px;
    width: 100%;
    margin: 0 auto 30px;
    padding-bottom: 3px;
    text-align: center;
    font-weight: 700;
    font-size: 18px;
    color: var(--orange-color);
    transition: color .3s ease-in-out;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background-color: var(--red-color);
      transform: scale(0);
      transition: transform .3s ease-in-out;
    }

    &:hover {
      color: var(--red-color);
      &::before {
        transform: scale(1);
      }
    }
  }

  &__text {
    margin-bottom: var(--mb-1);
  }
}

.swiper-slide {
  grid-column: 4 span;
}



@media (max-width: 1000px) {
  .calendar__slider .swiper-wrapper {
    display: flex;
    column-gap: 0;
  }
  .calendar {
    &__slider {
      padding-bottom: 100px;
    }
    &__item {
      min-height: 650px;
    }
  }
}


